.CheckoutSummary {
  text-align: center;
  width: 80%;
  margin: auto;
  margin-bottom: 0.375rem;
}

@media (min-width: 750px) {
  .CheckoutSummary {
    width: 500px;
  }
}

.spaced {
  letter-spacing: 3px;
}

#button-cancel-checkout {
  font-family: "Special Elite", serif;
  margin: 2rem 5px 0.5rem;
  background-color: #85b06b;
  border-color: #85b06b;
  color: #fcf5e6;
  padding: 0.7rem 0.75rem 0.375rem;
}

#button-cancel-checkout:hover:enabled {
  color: #fcf5e6;
  background-color: #444443;
  border-color: #85b06b;
}

#button-continue {
  color: #fcf5e6;
  font-family: "Special Elite", serif;
  margin: 2rem 5px 0.5rem;
  padding-top: 8px;
  padding: 0.7rem 0.75rem 0.375rem;
}

#button-continue:hover:enabled {
  background-color: #444443;
}

.container-burger-sm {
  overflow: scroll;
  height: 230px;
  display: flex;
  justify-content: center;
  scrollbar-width: none;
}
.container-burger-sm::-webkit-scrollbar {
  display: none;
}
