.ContactData {
  margin: 20px auto;
  width: 80%;
  text-align: center;
  box-shadow: 0 2px 3px #85b06b;
  border: 1px solid #85b06b;
  padding: 10px;
  box-sizing: border-box;
  background-color: #444443;
  color: #fff;
}

input {
  display: block;
}

.spaced {
  letter-spacing: 3px;
}

#button-order {
  color: #fcf5e6;
  font-family: "Special Elite", serif;
  margin: 5px 0.5rem;
  padding-top: 8px;
  /* padding: 0.7rem 0.75rem 0.375rem;  */
}

#button-order:hover:enabled {
  color: #4db30e;
  background-color: #f8edd6;
}
